@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body {
    font-family: 'Fredoka One', sans-serif;
    background-color: #1f2327;
    background-position: top;
    mso-border-shadow: yes;
}




#social-icon {
    color: #80ea6e;
}

#nav-bar {
    color: #80ea6e;
    background-color: #1b1f23;
//border: 2px solid #80ea6e;
}

#nav-toggle {
    background-color: #80ea6e;
}

#nav-item {
    color: #ffffff;
    text-align: center;
}
h2 {
    color: #6db33f;;
    text-align: center;
}

.project-content {
    padding: 2vh 15vw;
    color: white;
}

.project-description {
    font-family: 'Cantarell', sans-serif;
    font-weight: 800;
}

.aligned {
    text-align: center;
}

.tags {
    font-weight: bold;
    text-align: left;
}

.tag {
    display: inline-block;
    height: 2.3vh;
    font-weight: 500;
    font-size: medium;
    color: rgba(0, 0, 0, 0.6);
    line-height: 2vh;
    padding: 0 0.5vh;
    border-radius: 0.3vw;
    background-color: #e4e4e4;
    margin-bottom: 0.5vh;
    margin-right: 0.5vh;
    transition: all .15s ease-in-out;
}

.project-content a {
    text-decoration: none;
    font-size: small;
    text-align: center;
    color: white;
    border-radius: 5vw;
    padding: 0.9vh;
    background-color: #007bff;
}

.project-content a:hover {
    transition: 250ms;
    color: #c1c1c1;
    background-color: #0057c1;
}

#about-me {
    background-color: #262a2d;
}

#about-me-menu {
}

#about-me-content {
    color: #ffffff;
}

#about-me-content h3{
    color: #6db33f;
}

#about-me-content blockquote{
    font-family: 'Cantarell Extra Bold', sans-serif;
}
